@import '../../index.scss';

.project {
  display: flex;
  text-decoration: none;
  color: $white;
  border-radius: 10px;
  overflow: hidden;
  @include transition;
 
  max-height: 175px;
  //background: $dark-purple;
  @include shadow;
  border: 3px solid $dark-purple;
  .project-image {
    min-width:175px;
    img {
      width: 100%;
    }
  }
  .project-info {
    min-width: 50%;
    padding: 15px;
    .project-title, .project-description {
      max-width: 100%;
      white-space: pre-wrap;
      word-wrap: break-word; 
    }
    .project-title {
      font-weight: 600;
      color: $yellow!important;
    }
    
  }

  @media screen and (max-width: 500px) {
    flex-direction: column;
    max-height: none;
    .project-image {
      min-width: 100%;
    }
    .project-info {
      min-width: 100%;
      white-space: pre-wrap;
      width: 100%;
      min-width: 0;
      max-width: 80vw;
      display: flex;
      flex-direction: column;
    }
  }

  &:hover {
    background: $dark-purple;
  }
}